button.checkbox {
  width: 19%;
  margin-right: 1%;
  margin-bottom: 5px;
  padding: 5px 10px;
  background-color: transparent;
  border: 2px solid rgba(255, 255, 255, 1);
  color: #FFFFFF;
  font-size: 13.33px;
  font-weight: normal;
  position: relative;
  top: 0;
  cursor: pointer;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
  opacity: 0.8;
  -webkit-transition: all 0.2s;
  -o-transition: all 0.2s;
  transition: all 0.2s;
}

button.checkbox:hover {
  opacity: 1;
  -webkit-box-shadow: 0px 0px 5px rgba(255, 255, 255, .6);
          box-shadow: 0px 0px 5px rgba(255, 255, 255, .6);
}

button.checkbox.checked {
  opacity: 1;
  top: -3px;
  border: 2px solid rgba(255, 255, 255, 1);
  background-color: rgba(255, 255, 255, .3);
  -webkit-box-shadow: 0px 0px 7px rgba(255, 255, 255, .8);
          box-shadow: 0px 0px 7px rgba(255, 255, 255, .8);
}

@media screen and (max-width : 767px) {
  button.checkbox {
    width: 24%;
  }
}


